h2{
  background-color: black;
  color: white;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.thumbnail{
  width: 100%;
  aspect-ratio: 4 / 3;
  object-fit:cover;
}
@media screen and (min-width: 700px){
  .thumbnail {
    width: 40%;
  }
}

.thumbnail-date{
  font-size: 0.5rem;
}

.youtube {
  position: relative;
  height: 0;
  padding: 0 0 56.25%;
  overflow: hidden;
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.blog-body img {
  width: 60%;
  height: 60%;
}

@media screen and (max-width: 700px) {
  /* 480px以下に適用されるCSS（スマホ用） */
  .blog-body img {
    width: 100%;
    height: 100%;
  }
}


ol{
  padding: 0.5em 3em;
  margin: 1em 0;
  font-weight: bold;
  color: #6091d3;/*文字色*/
  background: #f0f8ff;
  border: solid 3px #6091d3;/*線*/
  border-radius: 10px;
  li {
    padding: 0.5rem
  }
}

.description {
  padding: 0.5em 3em;
  margin: 1em 0;
  color: black;/*文字色*/
  background: #E6E6E6;
  border: solid 1px #000000;/*線*/
}
